import { Component, OnInit, Input } from '@angular/core';
import * as _ from 'lodash';

import { Form } from '../form-factory/form';
import { ValidationFactory } from '../form-factory/validation.factory';
import { NodeBase, LeafNode } from '../form-factory/form-node';
import { QuestionGroup } from '../question-models/group-question';
import { FormErrorsService } from '../services/form-errors.service';

@Component({
  selector: 'error-renderer',
  template: `<div *ngIf="showErrors()" class="container">
  <ul class="list-group">
    <li
      class="list-group-item list-group-item-warning"
      *ngFor="let errorNode of errorNodes"
      (click)="announceErrorField(errorNode)"
    >
      <div class="error" *ngIf="errorNode.control.valid == false">
        <h4>{{ errorNode.question.label }}</h4>
        <span class="text-danger"> {{ getControlError(errorNode) }}</span>
      </div>
    </li>
  </ul>
</div>
`,
  styles: [`ul{list-style:none}.list-group-item{padding:2px 15px;cursor:pointer}ul li:hover{background-color:#fff}h4{margin-top:7px;margin-bottom:7px}`]
})
export class ErrorRendererComponent implements OnInit {
  @Input() form: Form;

  constructor(
    private validationFactory: ValidationFactory,
    private formErrorsService: FormErrorsService
  ) {}

  ngOnInit() {}

  showErrors() {
    return !this.form.valid && this.form.showErrors;
  }

  get errorNodes() {
    const invalidControls = this.form.markInvalidControls(
      this.form.rootNode,
      []
    );
    return invalidControls;
  }

  getControlError(node: LeafNode) {
    const errors: any = node.control.errors;

    if (errors) {
      return this.validationFactory.errors(errors, node.question);
    }

    return [];
  }

  announceErrorField(errorNode: LeafNode) {
    const nodes: Array<NodeBase> = this.form.searchNodeByQuestionId(
      errorNode.path.substring(0, errorNode.path.indexOf('.'))
    );

    _.forEach(nodes, (node: NodeBase) => {
      if (node.question.renderingType === 'page') {
        const pageIndex: number = this.getPageIndex(node);
        this.formErrorsService.announceErrorField(
          pageIndex + ',' + errorNode.question.key
        );
      }
    });
  }

  getPageIndex(node: NodeBase) {
    const questionGroup: QuestionGroup = this.form.rootNode
      .question as QuestionGroup;
    return questionGroup.questions.indexOf(node.question);
  }
}
