import {
  Component,
  OnInit,
  Input,
  forwardRef,
  EventEmitter,
  Output
} from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import * as moment_ from 'moment';

const Moment = moment_;

@Component({
  selector: 'date-time-picker',
  template: `<div class="row">
  <div *ngIf="!showTime" class="col-xs-12 col-md-12">
    <input
      *ngIf="!showWeeks"
      type="text"
      class="form-control"
      [value]="value | date: 'mediumDate'"
      (focus)="toggleDatePicker(true)"
      readonly
      placeholder="Select Date"
    />
    <div *ngIf="showWeeks" class="input-group">
      <input
        type="text"
        class="form-control"
        class="form-control"
        [value]="value | date: 'mediumDate'"
        (focus)="toggleDatePicker(true)"
        readonly
        placeholder="Select Date"
      />
      <div class="input-group-btn">
        <button
          type="button"
          class="btn btn-default dropdown-toggle"
          data-toggle="dropdown"
          aria-haspopup="true"
          aria-expanded="false"
        >
          Weeks <span class="caret"></span>
        </button>
        <ul class="dropdown-menu up">
          <li (click)="weeksSelected(count)" *ngFor="let count of weeks">
            <span> {{ count }} Weeks</span>
          </li>
        </ul>
      </div>
    </div>
  </div>
  <div *ngIf="showTime" class="col-xs-8 col-md-8">
    <input
      *ngIf="!showWeeks"
      type="text"
      class="form-control"
      [value]="value | date: 'mediumDate'"
      (focus)="toggleDatePicker(true)"
      readonly
      placeholder="Select Date"
    />
    <div *ngIf="showWeeks" class="input-group">
      <input
        type="text"
        class="form-control"
        class="form-control"
        [value]="value | date: 'mediumDate'"
        (focus)="toggleDatePicker(true)"
        readonly
        placeholder="Select Date"
      />
      <div class="input-group-btn">
        <button
          type="button"
          class="btn btn-default dropdown-toggle"
          data-toggle="dropdown"
          aria-haspopup="true"
          aria-expanded="false"
        >
          Weeks <span class="caret"></span>
        </button>
        <ul class="dropdown-menu up">
          <li (click)="weeksSelected(count)" *ngFor="let count of weeks">
            <span> {{ count }} Weeks</span>
          </li>
        </ul>
      </div>
    </div>
  </div>
  <div *ngIf="showTime" class="col-xs-4 col-md-4">
    <input
      type="text"
      class="form-control"
      [value]="value | date: 'shortTime'"
      (focus)="toggleTimePicker(true)"
      readonly
      placeholder="Select Time"
    />
  </div>
</div>
<date-picker
  *ngIf="showDatePicker"
  [initDate]="value"
  (onSelectDate)="setDate($event)"
  (onDatePickerCancel)="toggleDatePicker($event)"
></date-picker>

<time-picker
  *ngIf="showTimePicker"
  [initTime]="value"
  [use12Hour]="true"
  (onSelectTime)="setTime($event)"
  (onTimePickerCancel)="toggleTimePicker($event)"
></time-picker>
`,
  styles: [`input[readonly]{background-color:#fff}.up{bottom:100%!important;top:auto!important}.glyphicon{top:1px}`],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => DateTimePickerComponent),
      multi: true
    }
  ]
})
export class DateTimePickerComponent implements OnInit, ControlValueAccessor {
  @Input() modelValue: any;
  @Input() showDate = true;
  @Input() showTime = false;
  @Input() showWeeks = false;
  @Input() weeks: number[] = [2, 4, 6, 8, 12, 16, 24];
  @Output() onDateChange = new EventEmitter<any>();
  public showDatePicker = false;
  public showTimePicker = false;
  onChange: any = () => {};
  onTouched: any = () => {};

  constructor() {}

  ngOnInit() {}

  weeksSelected(count) {
    const now = new Date();
    const nextDate = now.setDate(now.getDate() + count * 7);
    this.value = Moment(nextDate).format();
  }
  setDate(date: any): void {
    if (date && date !== '') {
      this.value = Moment(date).format();
    } else {
      this.value = date;
    }
  }

  setTime(time: any): void {
    if (time && time !== '') {
      this.value = Moment(time).format();
    } else {
      this.value = time;
    }
    return;
  }

  toggleDatePicker(status: boolean): void {
    this.showDatePicker = status;
    /*setTimeout(function() {
        let _body = document.getElementById('content-wrapper').getBoundingClientRect(),
          elem = document.getElementById('section-modal-main');
        if (elem) {
          let elemBox = elem.getBoundingClientRect();
          if (elemBox.bottom > _body.bottom) {
            elem.style.bottom = '36px';
          }
        }
      }, 0);*/
    return;
  }

  toggleTimePicker(status: boolean): void {
    this.showTimePicker = status;
    return;
  }
  get value() {
    return this.modelValue;
  }

  set value(val) {
    this.modelValue = val;
    this.onDateChange.emit(val);
    this.onChange(val);
    this.onTouched();
  }

  registerOnChange(fn) {
    this.onChange = fn;
  }

  registerOnTouched(fn) {
    this.onTouched = fn;
  }

  writeValue(value) {
    if (value instanceof Date) {
      this.value = Moment(value).format();
    } else {
      this.value = value;
    }
  }
}
